import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


// import Layout from '@/views/layout'
import layout from '@/views/layout'

export default new Router({
  mode: 'history',
  scrollBehavior: () => ({y: 0}),
  routes: [
    { path: '/login', component: () => import('@/views/login') },
    { path: '/404', component: () => import('@/views/404') },
    {
      path: '/',
      component: layout,
      redirect: '/index',
      name: 'home',
      children: [
        {//首页
          path: '/index',
          component: () => import('@/views/index'),
        },
        {//专家库
          path: '/expert',
          component: () => import('@/views/expert/index')
        },
        {//多井管理
          path: '/multi',
          redirect: '/multi/video',
          component: () => import('@/views/multi/index'),
          children: [
            {
              path: 'video',
              component: () => import('@/views/multi/video')
            },
            {
              path: 'parameter',
              component: () => import('@/views/multi/parameter')
            },
            {
              path: 'line',
              component: () => import('@/views/multi/line')
            },
            {
              path: 'warn',
              component: () => import('@/views/multi/warn')
            },
            {
              path: 'watch',
              component: () => import('@/views/multi/watch')
            },
            {
              path: 'time',
              component: () => import('@/views/multi/time')
            },
            {
              path: 'bit',
              component: () => import('@/views/multi/bit')
            },
            {
              path: 'complex',
              component: () => import('@/views/multi/complex')
            },
            {
              path: 'count',
              component: () => import('@/views/multi/count')
            }
          ]
        },
        {//个人中心
          path: '/user',
          redirect: '/user/base',
          component: () => import('@/views/user/index'),
          children: [
            {
              path: 'base',
              component: () => import('@/views/user/base')
            },
            {
              path: 'pwd',
              component: () => import('@/views/user/pwd')
            },
            {
              path: 'apply',
              component: () => import('@/views/user/apply')
            },
            {
              path: 'review',
              component: () => import('@/views/user/review')
            },
            {
              path: 'auth',
              component: () => import('@/views/user/auth')
            },
            {
              path: 'account',
              component: () => import('@/views/user/account')
            }
          ]
        },
        {//井基础信息
          path: '/well',
          redirect: '/well/base',
          component: () => import('@/views/well/index'),
          children: [
            {
              path: 'base',
              component: () => import('@/views/well/base')
            },
            {
              path: 'lithology',
              component: () => import('@/views/well/lithology')
            },
            {
              path: 'gsald',
              component: () => import('@/views/well/gsald')
            },
            {
              path: 'drilled',
              component: () => import('@/views/well/drilled')
            },
            {
              path: 'equipment',
              component: () => import('@/views/well/equipment')
            },
            {
              path: 'welldepth',
              component: () => import('@/views/well/welldepth')
            },
            {
              path: 'wellhead',
              component: () => import('@/views/well/wellhead')
            },
            {
              path: 'welldata',
              component: () => import('@/views/well/welldata')
            },
            // {
            //   path: 'designtrack',
            //   component: () => import('@/views/well/designtrack')
            // },
            // {
            //   path: 'mapdisplay',
            //   component: () => import('@/views/well/mapdisplay')
            // },
            {
              path: 'schedule',
              component: () => import('@/views/well/schedule')
            }
          ]
        },
        {//井眼轨迹
          path: '/trail',
          redirect: '/trail/profile',
          component: () => import('@/views/trail/index'),
          children: [
            {
              path: 'profile',
              component: () => import('@/views/trail/profile')
            },
            // {
            //   path: 'designtrack',
            //   component: () => import('@/views/trail/designtrack')
            // },
            {
              path: 'mapdisplay',
              component: () => import('@/views/trail/mapdisplay')
            },
            {
              path: 'migration',
              component: () => import('@/views/trail/migration')
            }
          ]
        },
        {//生产数据记录
          path: '/record',
          redirect: '/record/bit',
          component: () => import('@/views/record/index'),
          children: [
            {
              path: 'bit',
              component: () => import('@/views/record/bit')
            },
            {
              path: 'powerdrill',
              component: () => import('@/views/record/powerdrill')
            },
            {
              path: 'centralizer',
              component: () => import('@/views/record/centralizer')
            },
            {
              path: 'assembly',
              component: () => import('@/views/record/assembly')
            },
            {
              path: 'devicefailuredata',
              component: () => import('@/views/record/devicefailuredata')
            },
            {
              path: 'drillingfluidrecording',
              component: () => import('@/views/record/drillingfluidrecording')
            },
            {
              path: 'floorburstandpressuretest',
              component: () => import('@/views/record/floorburstandpressuretest')
            },
            {
              path: 'reservoirprotection',
              component: () => import('@/views/record/reservoirprotection')
            },
            {
              path: 'drillingcoring',
              component: () => import('@/views/record/drillingcoring')
            }
          ]
        },
        {//报表管理
          path: '/report',
          redirect: '/report/drilling',
          component: () => import('@/views/report/index'),
          children: [
            {
              path: 'drilling',
              component: () => import('@/views/report/drilling')
            },
            {
              path: 'drillingfluid',
              component: () => import('@/views/report/drillingfluid')
            },
            {
              path: 'iufo',
              component: () => import('@/views/report/iufo')
            },
            {
              path: 'monthly',
              component: () => import('@/views/report/monthly')
            },
            {
              path: 'annual',
              component: () => import('@/views/report/annual')
            }
          ]
        },
        {//技术支持
          path: '/technical',
          redirect: '/technical/bitselection',
          component: () => import('@/views/technical/index'),
          children: [
            {
              path: 'bitselection',
              component: () => import('@/views/technical/bitselection')
            },
            {
              path: 'densitycurve',
              component: () => import('@/views/technical/densitycurve')
            },
            {
              path: 'drillingfluid',
              component: () => import('@/views/technical/drillingfluid')
            },
            {
              path: 'newprocess',
              component: () => import('@/views/technical/newprocess')
            },
            {
              path: 'remotediagnosis',
              component: () => import('@/views/technical/remotediagnosis')
            },
            {
              path: 'remotesupport',
              component: () => import('@/views/technical/remotesupport')
            },
            {
              path: 'trajectory',
              component: () => import('@/views/technical/trajectory')
            }
          ]
        },
        {//随钻跟踪
          path: '/tracking',
          redirect: '/tracking/earlywarning',
          component: () => import('@/views/tracking/index'),
          children: [
            {
              path: 'earlywarning',
              component: () => import('@/views/tracking/earlywarning')
            },
            {
              path: 'parameter',
              component: () => import('@/views/tracking/parameter')
            },
            {
              path: 'videomonitor',
              component: () => import('@/views/tracking/videomonitor')
            },
            {
              path: 'fluidcurve',
              component: () => import('@/views/tracking/fluidcurve')
            },
            {
              path: 'wellcomplex',
              component: () => import('@/views/tracking/wellcomplex')
            },
            {
              path: 'work',
              component: () => import('@/views/tracking/work')
            }
          ]
        },
        {//单井统计
          path: '/singlewell',
          redirect: '/singlewell/borebit',
          component: () => import('@/views/singlewell/index'),
          children: [
            {
              path: 'borebit',
              component: () => import('@/views/singlewell/borebit')
            },
            {
              path: 'screw',
              component: () => import('@/views/singlewell/screw')
            },
            {
              path: 'drillingtime',
              component: () => import('@/views/singlewell/drillingtime')
            },
            {
              path: 'complex',
              component: () => import('@/views/singlewell/complex')
            },
            {
              path: 'performance',
              component: () => import('@/views/singlewell/performance')
            }
          ]
        },
        {//完井数据
          path: '/wellcompletion',
          redirect: '/wellcompletion/casingdata',
          component: () => import('@/views/wellcompletion/index'),
          children: [
            {
              path: 'casingdata',
              component: () => import('@/views/wellcompletion/casingdata')
            },
            {
              path: 'casingstring',
              component: () => import('@/views/wellcompletion/casingstring')
            },
            {
              path: 'welloperation',
              component: () => import('@/views/wellcompletion/welloperation')
            },
            {
              path: 'completiongear',
              component: () => import('@/views/wellcompletion/completiongear')
            },
            {
              path: 'casingpressure',
              component: () => import('@/views/wellcompletion/casingpressure')
            },
            {
              path: 'caliperdata',
              component: () => import('@/views/wellcompletion/caliperdata')
            },
            {
              path: 'wellbasicdata',
              component: () => import('@/views/wellcompletion/wellbasicdata')
            }
          ]
        },
        {//复杂及故障
          path: '/complexfault',
          redirect: '/complexfault/leakage',
          component: () => import('@/views/complexfault/index'),
          children: [
            {
              path: 'leakage',
              component: () => import('@/views/complexfault/leakage')
            },
            {
              path: 'wellcontrol',
              component: () => import('@/views/complexfault/wellcontrol')
            },
            {
              path: 'downhole',
              component: () => import('@/views/complexfault/downhole')
            },
            {
              path: 'cardinstrument',
              component: () => import('@/views/complexfault/cardinstrument')
            },
            {
              path: 'collapse',
              component: () => import('@/views/complexfault/collapse')
            },
            {
              path: 'holeshrinkage',
              component: () => import('@/views/complexfault/holeshrinkage')
            },
            {
              path: 'cementing',
              component: () => import('@/views/complexfault/cementing')
            },
            {
              path: 'controlcomplexity',
              component: () => import('@/views/complexfault/controlcomplexity')
            },
            {
              path: 'repair',
              component: () => import('@/views/complexfault/repair')
            }
          ]
        },
        {//系统管理
          path: '/systemset',
          redirect: '/systemset/usermanage',
          component: () => import('@/views/systemset/index'),
          children: [
            {
              path: 'usermanage',
              component: () => import('@/views/systemset/usermanage')
            },
            {
              path: 'rolemanage',
              component: () => import('@/views/systemset/rolemanage')
            },
            {
              path: 'peopleaudit',
              component: () => import('@/views/systemset/peopleaudit')
            },
            {
              path: 'knowledgeup',
              component: () => import('@/views/systemset/knowledgeup')
            }
          ]
        }
      ]
    },
    { path: '*', redirect: '/404', },
    // {
    //   path: '/',
    //   component: Layout,
    //   name: 'home',
    //   children: [
    //     {
    //       path: '',
    //       component: () => import('@/views/home/index'),
    //       meta: {
    //         title: '首页',
    //         icon: 'tree'
    //       }
    //     }
    //   ]
    // }
  ]
})



